const MsgList = [
  "魔法少女白澪ㄟ(≧◇≦)ㄏ",
  "一个魔法少女(^^ゞ",
  "a bad god！",
  "a P.R.C. Citizen！"
];
const displayText = ref('');
let index = 0;
function startTyping() {
  const currentMsg = MsgList[index % MsgList.length];
  let charIndex = 0;

  const intervalId = setInterval(() => {
    displayText.value += currentMsg[charIndex];

    if (charIndex === currentMsg.length - 1) {
      clearInterval(intervalId);
      setTimeout(() => {
        eraseText();
      }, 1000);
    }

    charIndex++;
  }, 200);
}
function eraseText() {
  const intervalId = setInterval(() => {
    displayText.value = displayText.value.slice(0, -1);

    if (displayText.value === '') {
      clearInterval(intervalId);
      index++;
      setTimeout(() => {
        startTyping();
      }, 1000);
    }
  }, 100);
}
onMounted(() => {
  watch(() => index, () => {
    startTyping();
  }, { immediate: true });
});


